import React from 'react';
import styled from 'styled-components';
import { Link } from 'gatsby';
import Container from './container';
import { useStaticQuery, graphql } from 'gatsby';
import 'antd/dist/antd.css';
import { Row, Col } from 'antd';
import { InstagramFilled, LinkedinFilled, YoutubeFilled, FacebookFilled, CarryOutFilled } from '@ant-design/icons';

const Header = () => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
          }
        }
      }
    `
  );

  const iconStyle = {
    fontSize: "24px"
  }

  // adjust footer


  return (
    <StyledHeader>
      <HeaderWrapper>
        <HeaderTitle>
          <Link to="/" style={{ color: "#013b89" }}><img src="/media/logo.png" alt="Pre-Law" style={{ maxWidth: "50px", margin: "0", display: "inline-block" }}/><StyledP>{site.siteMetadata.title}</StyledP></Link>
        </HeaderTitle>

        <HeaderNavList>
          <HeaderNavListItem>
            <Link to="/"
            activeClassName="activeHeaderButton">Home</Link>
          </HeaderNavListItem>

          <HeaderNavListItem>
            <Link to="/about"
            activeClassName="activeHeaderButton">About</Link>
          </HeaderNavListItem>

          <HeaderNavListItem>
            <Link to="/blog"
            activeClassName="activeHeaderButton">Blog</Link>
          </HeaderNavListItem>

          <HeaderNavListItem>
            <Link to="/sessions"
            activeClassName="activeHeaderButton">Sessions</Link>
          </HeaderNavListItem>

          {/* <HeaderNavListItem>
            <Link to="/join"
            activeClassName="activeHeaderButton">Join</Link>
          </HeaderNavListItem> */}

          <HeaderNavListItem>
            <Link to="/team"
            activeClassName="activeHeaderButton">Team</Link>
          </HeaderNavListItem>

          <HeaderNavListItemIcon>
            <Link to="https://www.instagram.com/prelawshadowers/"
            target="_blank"
            activeClassName="activeHeaderButton"><InstagramFilled style={iconStyle}/></Link>
          </HeaderNavListItemIcon>

          <HeaderNavListItemIcon>
            <Link to="https://www.youtube.com/channel/UCIj1Pq85DCEol0lOr-Jl7qQ"
            target="_blank"
            activeClassName="activeHeaderButton"><YoutubeFilled style={iconStyle}/></Link>
          </HeaderNavListItemIcon>

          <HeaderNavListItemIcon>
            <Link to="https://www.linkedin.com/company/pre-law-shadowers/"
            target="_blank"
            activeClassName="activeHeaderButton"><LinkedinFilled style={iconStyle}/></Link>
          </HeaderNavListItemIcon>

          <HeaderNavListItemIcon>
            <Link to="https://www.facebook.com/Pre-Law-Shadowers-103498681960235"
            target="_blank"
            activeClassName="activeHeaderButton"><FacebookFilled style={iconStyle}/></Link>
          </HeaderNavListItemIcon>

          <HeaderNavListItemIcon>
            <Link to="https://prelawshadowers.carrd.co/"
            target="_blank"
            activeClassName="activeHeaderButton"><CarryOutFilled style={iconStyle}/></Link>
          </HeaderNavListItemIcon>

          {/* <HeaderNavListItem>
            <Link to="/contact"
            activeClassName="activeHeaderButton">Contact</Link>
          </HeaderNavListItem> */}
        </HeaderNavList>
      </HeaderWrapper>
    </StyledHeader>
  );
};

export default Header;

const HeaderNavList = ({ children }) => {
  return (
    <StyledNav>
      <StyledNavList>{children}</StyledNavList>
    </StyledNav>
  );
};

const HeaderNavListItem = ({ children }) => {
  return <StyledNavListItem>{children}</StyledNavListItem>;
};

const HeaderNavListItemIcon = ({ children }) => {
  return <StyledNavListItemIcon>{children}</StyledNavListItemIcon>;
};

const StyledP = styled.p`
  display: initial;
  @media screen and (max-width: 600px) {
    display: none;
  }
`;

const StyledHeader = styled.header`
  background-color: #ffda59;
  padding: 15px 20px;
  position: fixed;
  top: 0;
  width: 100%;
  overflow: hidden;
  z-index: 1000;

  @media screen and (max-width: 600px) {
    padding: 15px 15px;
  }
`;

const HeaderWrapper = styled(Container)`
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const HeaderTitle = styled.div`
  & a {
    text-transform: uppercase;
    text-decoration: none;
    font-size: var(--size-400);
    font-family: 'Roboto', sans-serif;
    font-weight: 400;
    color: "#013b89";
  }
`;

const StyledNav = styled.nav`
  position: static;
  padding: 0;
  background: transparent;
  backdrop-filter: unset;
`;

const StyledNavList = styled.ul`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 0;
  height: 40px;
  margin: 0px;
  list-style-type: none;
`;

const StyledNavListItem = styled.li`
  margin-right: 0.5rem;
  
  @media screen and (max-width: 600px) { 
    margin-right: 0;
  }
  // @media screen and (max-width: 700px) {
  //   &:not(:last-of-type) {
  //     margin-right: 1rem;
  //   }
  // }
  & a {
    color: inherit;
    text-transform: uppercase;
    font-size: var(--size-300);
    text-decoration: none;
    letter-spacing: 0.1rem;
    padding: 10px 5px !important;
    transition: 1s;
  }
  & a:hover {
    background-color: #013b89;
    color: #ffda59 !important;
    transition: 0.2s;
  }
  & a:active {
    background-color: #013b89;
    color: #ffda59 !important;
    transition: 0.2s;
  }
  @media screen and (max-width: 700px) {
    & a {
      font-size: 0.7rem;
    }
  }
`;

const StyledNavListItemIcon = styled.li`
  &:not(:last-of-type) {
    margin-right: 0.25rem;
  }
  @media screen and (max-width: 870px) {
    display: none;
  }
  @media screen and (max-width: 700px) {
    &:not(:last-of-type) {
      margin-right: 0.25rem;
    }
  }
  & a {
    color: #013b89;
    text-transform: uppercase;
    font-size: var(--size-300);
    text-decoration: none;
    letter-spacing: 0.1rem;
    padding: 5px !important;
    transition: 1s;
  }
  & a:hover {
    color: #008fe1 !important;
    transition: 0.2s;
  }
  @media screen and (max-width: 700px) {
    & a {
      font-size: 0.7rem;
    }
  }
`;
